export const isDef = (v) => v != null;
export const toLowerCase = (str) => str.toLowerCase();
export const toDashCase = (str) => toLowerCase(str.replace(/([A-Z0-9])/g, g => ' ' + g[0]).trim().replace(/ /g, '-'));
export const dashToPascalCase = (str) => toLowerCase(str).split('-').map(segment => segment.charAt(0).toUpperCase() + segment.slice(1)).join('');
export const toTitleCase = (str) => str.charAt(0).toUpperCase() + str.substr(1);
export const captializeFirstLetter = (str) => str.charAt(0).toUpperCase() + str.slice(1);
export const noop = () => { };
export const pluck = (obj, keys) => {
    return keys.reduce((final, key) => {
        if (obj[key]) {
            final[key] = obj[key];
        }
        return final;
    }, {});
};
export const isObject = (val) => {
    return val != null && typeof val === 'object' && Array.isArray(val) === false;
};
